 @import '../../scss/variables.scss';
 
 .card {
    &_container {
      padding: 0.5cm;
      margin: 0;
      width: 9cm;
      height: 5cm;
      grid-area: 2 / 1 / 3 / 2;
      justify-self: center;
      align-self: center;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      overflow: hidden;
      border-radius: 20px;
      background-repeat: no-repeat;
      background-position: center center;
      background: linear-gradient(45deg, $background-main 0%, $background-second 50%, $background-main 51%, $background-second 100%);
      background-size: cover;
      color: $text-main;
      box-shadow: $boxShadow;
    }
    &_title {
      margin: 0;
      height: 20px;
      width: max-content;
      align-self: flex-end;
    }
    &_number {
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: center;
      gap: 20px;
      font-size: 24px;
      list-style: none;
    }
    &_data_wrapper {
      display: flex;
      justify-content: space-between;
    }
    &_data {
      p {
        margin: 0;
        font-size: 16px;
        font-weight: 600;
      }
    }
  }